:root {
  --main-color: #6F83D4;
  --light-gray: #f0f0f0;
  --lighter-gray: #f8f8f8;
}

*, *::before, *::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background-color: var(--main-color);
  font-family: "Segoe UI", sans-serif;
}

.container {
  background-color: white;
  width: 500px;
  min-height: 80vh;
  margin: 70px auto;
  padding: 30px;
  border-radius: 8px;
  box-shadow: 0px 2px 5px rgb(0,0,0,.3);
  overflow: hidden;
}

header {
  background-color: white;
}

header h1 {
  text-align: center;
}

.btn {
  height: 30px;
  background-color: var(--main-color);
  outline: none;
  border: none;
  border-radius: 4px;
  color: white;
  padding: 5px;
  cursor: pointer;
}

.submit-btn {
  display: block;
  margin: 10px auto;
  width: 60%;
}

.user-input {
  background-color: white;
  padding: 10px;
}

.form-control label {
  font-weight: 500;
  display: block;
}

.form-control input {
  display: block;
  width: 100%;
  height: 40px;
  outline: none;
  border: 2px solid var(--light-gray);
  border-radius: 4px;
  margin-bottom: 10px;
  padding-left: 8px;
  padding-right: 8px;
}

.form-control input:focus {
  outline: none;
  border: 2px solid var(--main-color);
}

.form-control-checkbox input {
  margin-left: 4px;
  margin-right: 15px;
}

.form-control-checkbox input:checked {
  background-color: var(--main-color);
}

hr {
  border: 1px solid var(--light-gray);
}

.task {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: var(--lighter-gray);
  margin: 5px 0px;
  padding: 6px 16px;
  border-radius: 8px;
} 

.task-content {
  display: flex;
  justify-content: right;
}

.star-icon {
  height: 12px;
  padding: 3px 6px 0px 0px;
}

header img {
  display: block;
  width: 15%;
  margin: 0px auto;
}

@media (max-width: 600px) {
  .container {
    width: 80vw;
  }
}